import React from 'react';
import './styles.css';
import logo from '../../images/logo.png';
import { MdMenu } from "react-icons/md";
import './scripts/menu';
const Navbar = props => (
    <nav id ='navbar'>
        <div id="logo-container">
            
            <a id="logo" style={{marginTop: '40px'}} href="/"><img src={logo} alt="Logo PixelCoders" height="95"></img></a>
            <div id="#desktop-nav">
                <a className="nav-item" href="#home">Início</a>
                <a className="nav-item" href="#diferenciais">Diferenciais</a>
                <a className="nav-item" href="#contato">Contato</a>
            </div>
        </div>
        <div >
        <MdMenu id="burger" onClick={props.drawerClickHandler} />
        </div>
    </nav>
);

export default Navbar;