import React from 'react';
import './styles.css';
import { MdClose } from "react-icons/md";
const Drawer = props => (    
    <div id="drawer">
        <div id="drawerCloser">
                <MdClose onClick={props.drawerClickHandler} />
        </div>
        <a href="#home">Topo</a>
        <a href="#diferenciais">Diferenciais</a>
        <a href="#contato"> Contato</a>
    </div>
);

export default Drawer;